.heroBanner {
  background-image: url('../../assets/ravenous-banner.jpg');
  background-size: cover;
  background-position: top;
  height: 320px;
  text-align: center;
}

.filters {
  width: 60%;
  margin: 0 auto;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: center;
  list-style-type: none;
}

.options {
  color: #eee;
  font-weight: 600;
  font-size: .8rem;
  letter-spacing: .2px;
  font-size: 1rem;
  margin: 60px 20px 10px 20px;
  padding: 6px 12px;
  cursor: pointer;
  line-height: 1.4rem;
}

.options:hover {
  color:#B6943D;
  transition-duration: 1s;
}

.active {
  color:#B6943D;
}

.inputs {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.input {
  padding: 8px 100px 8px 4px;
}

.go {
background-color: #B6943D;
border: 1px solid #B6943D;
margin-top: 20px;
padding: 10px 40px;
border-radius: 5px;
color: #eee;
font-weight: 600;
cursor: pointer;
}

.go:hover {
  background-color: #9b7923;
  transition-duration: 1s;
}

.enterPressed {
  background-color: #9b7923;
}

@media screen and (max-width: 576px) {
  .inputs {
    flex-direction: column;
    align-items: center;
  }
}
